html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

@-webkit-keyframes logo {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.9);
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes logo {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.9);
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes bar_text-reveal {
  0% {
    width: 0;
    height: 4px;
  }
  20% {
    width: 5px;
    height: 4px;
  }
  100% {
    width: 5px;
    height: 59px;
  }
}

@keyframes bar_text-reveal {
  0% {
    width: 0;
    height: 4px;
  }
  20% {
    width: 5px;
    height: 4px;
  }
  100% {
    width: 5px;
    height: 59px;
  }
}

@-webkit-keyframes bar_text-move {
  0% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(-235px);
    transform: translateX(-235px);
  }
}

@keyframes bar_text-move {
  0% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(-235px);
    transform: translateX(-235px);
  }
}

@-webkit-keyframes revealText {
  0% {
    width: 0;
  }
  100% {
    width: 268px;
  }
}

@keyframes revealText {
  0% {
    width: 0;
  }
  100% {
    width: 268px;
  }
}

@-webkit-keyframes slow-shake {
  25% {
    -webkit-transform: translate(-3px, -2px) rotate(-0.15deg);
    transform: translate(-3px, -2px) rotate(-0.15deg);
  }
  50% {
    -webkit-transform: translate(2px, 1px) rotate(0deg);
    transform: translate(2px, 1px) rotate(0deg);
  }
  75% {
    -webkit-transform: translate(-2px, -2px) rotate(-0.15deg);
    transform: translate(-2px, -2px) rotate(-0.15deg);
  }
  100% {
    -webkit-transform: translate(0, 0) rotate(0deg);
    transform: translate(0, 0) rotate(0deg);
  }
}

@keyframes slow-shake {
  25% {
    -webkit-transform: translate(-3px, -2px) rotate(-0.15deg);
    transform: translate(-3px, -2px) rotate(-0.15deg);
  }
  50% {
    -webkit-transform: translate(2px, 1px) rotate(0deg);
    transform: translate(2px, 1px) rotate(0deg);
  }
  75% {
    -webkit-transform: translate(-2px, -2px) rotate(-0.15deg);
    transform: translate(-2px, -2px) rotate(-0.15deg);
  }
  100% {
    -webkit-transform: translate(0, 0) rotate(0deg);
    transform: translate(0, 0) rotate(0deg);
  }
}

@-webkit-keyframes background {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(-450px);
    transform: translateY(-450px);
  }
}

@keyframes background {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(-450px);
    transform: translateY(-450px);
  }
}

#background {
  -webkit-animation: background 0.9s 0s ease-in-out both;
  animation: background 0.9s 0s ease-in-out both;
}

#logo {
  -webkit-animation: logo 0.4s 0.7s ease-out both;
  animation: logo 0.4s 0.7s ease-out both;
}

#bar_text {
  -webkit-animation: bar_text-reveal 0.6s 0.5s ease-out both, bar_text-move 0.6s 1.1s ease-in-out forwards;
  animation: bar_text-reveal 0.6s 0.5s ease-out both, bar_text-move 0.6s 1.1s ease-in-out forwards;
}

#revealtext {
  -webkit-animation: revealText 0.6s 1.1s ease-in-out forwards;
  animation: revealText 0.6s 1.1s ease-in-out forwards;
}

#seadoo {
  -webkit-animation: slow-shake 10s 0s ease-in-out forwards;
  animation: slow-shake 10s 0s ease-in-out forwards;
}

#cta {
  -webkit-animation: fadeIn 0.4s 2.1s ease-in-out both;
  animation: fadeIn 0.4s 2.1s ease-in-out both;
}

body {
  -webkit-transform: rotateZ(0deg);
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

#content {
  position: absolute;
  width: 300px;
  height: 600px;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid #000;
  background-color: #fff;
  top: 0;
  left: 0;
  margin: auto;
  overflow: hidden;
}

#content #clicktag {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
}

#content * {
  position: absolute;
}

#content #seadoo {
  left: -2px;
  bottom: 0;
  -webkit-transform-origin: 30% 50%;
  transform-origin: 30% 50%;
}

#content #revealtext {
  top: 0px;
  right: 0px;
  height: 210px;
  overflow: hidden;
}

#content #text {
  right: 0;
}

#content #bar_text {
  top: 39px;
  left: 250px;
  background: #00aa90;
}

#content #logo {
  top: 528px;
  left: 89px;
}

#content:hover #cta {
  background-color: #fff;
  background-image: url("cta_hover.png");
}

#content #cta {
  top: 460px;
  left: 80px;
  width: 140px;
  height: 37px;
  background-color: #00aa90;
  background-image: url("cta.png");
  background-position: 16px 10px;
  background-repeat: no-repeat;
  border-radius: 1px;
}
